___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --el-color-primary: #4b82fa !important;
  --el-color-primary-lig: #d6e3ff !important;
  font-family: "Microsoft YaHei", "微软雅黑" !important;
  --cur-border: 1px solid #e7f1fd;
  --cur-font-color: #333333;
  --cur-font-color-lig: #666666;
  --cur-font-color-min: #999999;
  --cur-font-size-min: 12px;
  --cur-font-size-lig: 14px;
  --cur-font-size-max: 16px;
  --cur-letter-spacing: 0px;
  --cur-dialog-width: 460px;
  --cur-dialog-border-radius: 12px;
  --cur-dialog-border: 1px solid #f5f5f5;
  --cur-dialog-btn-width: 56px;
  --cur-dialog-btn-height: 24px;
}

.el-dialog {
  background: #ffffff;
  box-shadow: 0px 4px 20px 0px rgba(6, 10, 20, 0.1);
  border-radius: var(--cur-dialog-border-radius);

  .el-dialog__footer {
    padding-top: 14px;
  }
}

.el-dialog .el-dialog__body {
  border-top: var(--cur-dialog-border);
  border-bottom: var(--cur-dialog-border);
}

.el-dialog .el-dialog__footer .el-button {
  min-width: var(--cur-dialog-btn-width);
  min-height: var(--cur-dialog-btn-height);
}

.el-table th.el-table__cell {
  background-color: #f5f8fb !important;
  font-size: 12px;
  color: #444444;
  font-weight: 700;
}

.el-input__wrapper {
  background-image: linear-gradient(101deg, #f8f9fa 0%, #ffffff 100%);
  border-radius: 4px;
}

.el-button.is-link+.el-button.is-link {
  position: relative;
}

.el-button.is-link+.el-button.is-link::before {
  position: absolute;
  content: "";
  display: block;
  width: 1px;
  height: 10px;
  left: -7px;
  background: #e0e0e0;
}

.el-table *,
.el-dialog *,
.el-input__wrapper * {
  &:not(.left-h5 *) {
    font-size: 12px !important;
  }
}

.el-dialog .el-dialog__title {
  font-size: 14px !important;
}

.el-checkbox__label {
  color: #555555;
}

.ps {
  line-height: 1;
  font-size: 12px;
  color: #aeaeae;
  font-weight: 400;
}

.el-form-item__content .ps {
  width: 100%;
  margin-top: 6px;
  min-height: 20px;
}

.radio {
  width: 12px;
  height: 12px;
  text-align: center;
  line-height: 12px;
  border-radius: 50%;
  background: #d8d8d8;
  display: inline-block;
}

.el-dialog__body {
  border-top: 1px solid rgba(245, 245, 245, 1);
  border-bottom: 1px solid rgba(245, 245, 245, 1);
}

.el-popper.is-dark {
  background: #fff;
  border-color: #fff;
  color: #666666;
  ;
  box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.1);
}

.el-popper.is-dark .el-popper__arrow::before {
  background: #fff;
  border-color: #fff;
}
