import { createStore } from "vuex";

export default createStore({
  state: {
    dayStart:
      new Date(new Date(new Date().toLocaleDateString()).getTime()).getTime() /
      1000, // 当天零点
    loading: false, // 加载中
    pageKey: 10000, // 路由key
    siteInfo: {}, // 站点信息
    sysTem: "",
    sysLayOpt: 0, //0.默认、1.web统计、2.应用统计、3.APK、4.IOS
	  sysLayTab: 0, //标签顺序
    visible: false
  },
  getters: {},
  mutations: {
    SiteInfo(state, info) {
      state.siteInfo = info || {};
      state.num = info.tplDataNums || {};
    },
    Loading(state, val) {
      state.loading = val || false;
    },
    PageKey(state, sec) {
      state.pageKey = Math.random() * 1000;
    },
    changLayOpt(state, val) {
      state.sysLayOpt = val || 0;
    },
    setModel(state, val) {
      state.visible = val || false
    },
    changLayTab(state, val) {
      state.sysLayTab = val || 0;
    },
  },
  actions: {},
});
