// 日期格式
import useApi from "@/lib/js/api.js";
import datetime from "@/lib/js/date.js";
var ajax;
// 初始化app api地址
function initAppApi(app, obj) {
  obj = obj || {};
  var uri = {};
  if (location.protocol == "file:") {
    // 这种情况目前只在开发环境下发生，应该无需考虑
    var dfHost = "http://127.0.0.1";
    var data = prompt("请输入API服务", dfHost);
    if (data) {
      obj = new _URL(data);
    }
    uri = new _URL(dfHost);
  } else {
    // 默认当前地址
    uri = new _URL(location);
  }
  if (obj.protocol) uri.protocol = obj.protocol;
  if (obj.hostname) uri.hostname = obj.hostname;
  if (obj.port) uri.port = obj.port;
  var apiAddr = uri.origin;
  var ajaxHandle = ajax = httpFn(apiAddr);
  if (app) {
    // 所有站点公共组件
    app.config.globalProperties.$ajax = ajaxHandle;
    app.config.globalProperties.$Time = datetime;
  }
  var api = useApi(ajaxHandle);
  return api.getSiteInfo().then(function (res) {
    // 获取站点信息
    var data = res.data || {};
    // store.commit("SiteInfo",data) // 站点信息
    return store;
  });
  return store;
}

