const TOKEN_KEYTJ = "token_tj";
const TOKEN_KEYAPP = "token_app";
const TOKEN_KEYSHOP = "token_shop";
const USER = "userInfo_tj";
// token //统计
export const setToken = (token) => localStorage.setItem(TOKEN_KEYTJ, token);

export const getToken = () => localStorage.getItem(TOKEN_KEYTJ);

export const removeToken = () => localStorage.removeItem(TOKEN_KEYTJ);
// token //应用
export const setTokenApp = (token) => localStorage.setItem(TOKEN_KEYAPP, token);

export const getTokenApp = () => localStorage.getItem(TOKEN_KEYAPP);

export const removeTokenApp = () => localStorage.removeItem(TOKEN_KEYAPP);
// token //商城
export const setTokenShop = (token) => localStorage.setItem(TOKEN_KEYSHOP, token);

export const getTokenShop = () => localStorage.getItem(TOKEN_KEYSHOP);

export const removeTokenShop = () => localStorage.removeItem(TOKEN_KEYSHOP);
// uer
export const setUser = (data) =>
  localStorage.setItem(USER, JSON.stringify(data || ""));
export const getUser = () => localStorage.getItem(USER);
export const removeUser = () => localStorage.removeItem(USER);
// cms token
export const getCmsToken = () => localStorage.getItem("token");
// 设置当前系统Session
export const setSysSession = (sys) => sessionStorage.setItem('sys',sys)
export const getSysSession = () => sessionStorage.getItem('sys')
